import React, { Component } from 'react'
import Slider from 'react-slick'
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import mylifeCss from './MyLife.module.css'

export default function MyLife() {

    const settings = {
      dots: true,
      infinite: true,
      slidesToShow: 1,
      slidesToScroll: 1,
      autoplay: true,
      speed: 3000,
      autoplaySpeed: 2000,
      cssEase: "ease-in-out",
      variableWidth: true,
      centerMode: true
      
  };
  
  
  return (
    <div className={mylifeCss.myLife}>
      <p className={mylifeCss.mylife_text}>MY LIFE</p>
      <Slider {...settings} className={mylifeCss.sliderCss} style={{
        width:'80%',
        height:'480px',
        marginLeft:'10%',
        paddingRight: '100px',
        marginTop: '20px',
        textAlign:"center"
        }}>
        <div >
          <h3><img src={process.env.PUBLIC_URL+'/photo/xin_photo.jpg'}/></h3>
        </div>
        <div>
          <h3><img src={process.env.PUBLIC_URL+'/photo/與校長有約1.JPG'}/></h3>
        </div>
        <div >
          <h3><img src={process.env.PUBLIC_URL+'/photo/css-3.png'}/></h3>
        </div>
        <div >
          <h3><img src={process.env.PUBLIC_URL+'/photo/xin_photo.jpg'}/></h3>
        </div>
        <div >
          <h3><img src={process.env.PUBLIC_URL+'/photo/與校長有約1.JPG'}/></h3>
        </div>
      </Slider>
    </div>
  );

  
}
