import AboutMe from "./AboutMe";
import { BrowserRouter,HashRouter,RouterProps,Route, Routes,Link } from "react-router-dom";
import MyLife from "./MyLife";
import NavBar from "./NavBar";
import Portfolio from "./Portfolio"
import Tool from './Tool'


function App() {
  return (
    <HashRouter>
      <NavBar />
      <Routes>
        <Route path="/" element={<p>尚在設計中</p>}/>
        <Route path="/AboutMe/:sid?" element={<AboutMe/>}/>
        <Route path="/MyLife/:sid?" element={<MyLife/>}/>
        <Route path="/Portfolio/:sid?" element={<Portfolio />}/>
        <Route path="/Tool/:sid?" element={<Tool />}/>
        <Route path="*" element={<p>找不到頁面</p>}/>
      </Routes>
    </HashRouter>
  );

}

export default App;
