import React , { useEffect } from 'react'
import {Link, NavLink,useNavigate} from "react-router-dom";
import MyLife from "./MyLife";
import AboutMe from "./AboutMe";
import barCss from "./NavBar.module.css"
import Anchor from 'react-anchor-without-hash';
import { HashLink } from 'react-router-hash-link';

export default function NavBar() {

    const muneOnclick= () => {
        var muneIcon=document.getElementById('muneIcon');
        var sideBar=document.getElementById('bar');
        if(muneIcon.getAttribute('src',2)=='./photo/close.png'){
            muneIcon.setAttribute("src",'./photo/menu.png');
            sideBar.style.visibility='hidden';
            sideBar.style.right='-100px';
        }else{
            muneIcon.setAttribute("src",'./photo/close.png');
            sideBar.style.visibility='visible';
            sideBar.style.right='0';
        }
    }
    const scrollWithOffset = (el) => {
        const yCoordinate = el.getBoundingClientRect().top + window.pageYOffset;
        const yOffset = -80; 
        window.scrollTo({ top: yCoordinate + yOffset, behavior: 'smooth' }); 
    }
       

  return (
    <div className={barCss.navbar}>
        <header>
            <Link to="/" className={barCss.xin_logo}>Xinnn</Link>
            <img src={process.env.PUBLIC_URL+'/photo/menu.png'} className={barCss.menubtn} onClick={muneOnclick} id='muneIcon'/>
            <ul className={barCss.barul1} id='bar'>
                
                <li><Link to="/">首頁</Link></li>
                <li><HashLink to="/AboutMe#" smooth>關於我</HashLink>
                    <ul>
                        <li><HashLink to='/AboutMe#about_history' scroll={el => scrollWithOffset(el)}>個人經歷</HashLink></li>
                        <li><HashLink to='/AboutMe#about_study' scroll={el => scrollWithOffset(el)} >研習經歷</HashLink></li> 
                        <li><HashLink to='/AboutMe#about_price' scroll={el => scrollWithOffset(el)}>獲獎經歷</HashLink></li>
                    </ul>
                </li>
                {/*}
                <li><Link to="/MyLife">生活紀錄</Link>
                    <ul>
                        <li><HashLink to='/MyLife#mylife_active'>活動</HashLink></li>
                        <li><HashLink to='/MyLife#mylife_camp'>營隊</HashLink></li>
                        <li><HashLink to='/MyLife#mylife_other'>其他</HashLink ></li>
                    </ul>
                </li>
  */}
                <li><Link to="/Portfolio">作品集</Link>
                    <ul>
                        <li><HashLink to='/Portfolio#portfolio_class'>課程作品</HashLink ></li>
                        <li><HashLink to='/Portfolio#portfolio_game'>競賽作品</HashLink ></li>
                        {/*<li><HashLink to='/Portfolio#portfolio_work'>接案作品</HashLink></li>*/}
                    </ul>
                </li>
                <li><Link to="/Tool">小工具</Link>
                    <ul>
                        <li><HashLink to='/Tool#tool_link'>常用連結</HashLink ></li>
                        <li><HashLink to='/Tool#tool_timer'>計時器</HashLink ></li>
                        <li><HashLink to='/Tool#tool_drawLots'>抽籤系統</HashLink ></li>
                        <li><HashLink to='/Tool#tool_marquee'>文字跑馬燈</HashLink ></li>
                    </ul>
                </li>
                
            </ul>
      </header>
      <div className={barCss.hightset}></div>
    </div>
  )
}
